<template>
<div style="height: 100%">
    <div id="weiche">
        <gaodemap :points="coords" clusterer="true" :city="mapCityName"></gaodemap>
    </div>

    <div id="searchOilDiv">
        <ul class="list-group">
            <li class="list-group-item" v-for="station in coords">
                <h5>{{station.title}}</h5>
                <p>{{station.items[0].value}}</p>
                <p>电话：{{station.items[1].value}}</p>
            </li>
        </ul>
    </div>
    <label for="" class="label oilstationCount">搜索到：{{coords.length}}个油站</label>
    <div id="searchCity">
        <select class="form-control selectpicker"  v-select="allCitys" v-model.number="cityId" data-live-search="true">
        </select>
    </div>
</div>
</template>

<script type="text/javascript">
/* eslint eqeqeq: "off" */
import CommonTool from '../../Index/Lib/CommonTool.js'
import map from './Map.vue'

export default {
    components: {
        gaodemap: map
    },
    mounted: function () {
        // this.$dispatch('onPageTitleChanged', '微车油站地图')
        this.getCityList()
        this.getOilstationData()
    },
    data: function () {
        return {
            coords: [],
            allCitys: [],
            allOilstation: [],
            cityId: null,
            mapCityName: '',
            cityMap: []
        }
    },
    methods: {
        getOilstationData: function () {
            CommonTool.loading()
            var self = this
            $.ajax({
                url: '/weiche_cms/api/common/query_stations_no_test',
                type: 'GET',
                dataType: 'json'
            })
            .done(function (res) {
                self.allOilstation = res
                var tmp = {}
                self.coords = []
                res.forEach(function (elem) {
                    tmp = {}
                    tmp.lat = elem.lat
                    tmp.lon = elem.lon
                    tmp.items = []
                    tmp.title = elem.name
                    tmp.items.push({
                        key: '地址',
                        value: elem.address
                    }, {
                        key: '电话',
                        value: elem.phone
                    })
                    self.coords.push(tmp)
                })
            })
            .fail(function () {
                window.alert('服务器加载数据失败，请稍后再试')
                // location.reload()
            })
            .always(function () {
                CommonTool.loaded()
            })
        },
        getCityList: function () {
            CommonTool.loading()
            var self = this
            $.ajax({
                url: '/weiche_cms/api/common/query_cities',
                type: 'GET',
                dataType: 'json',
                data: {city_type: 0}
            })
            .done(function (res) {
                self.allCitys = []
                self.allCitys.push({key: '全国', value: 0})
                res.forEach(function (elem) {
                    self.cityMap[elem.id] = elem.city_name
                    self.allCitys.push({key: elem.city_name + '(' + elem.city_pinyin + ')', value: elem.id})
                })
                self.cityId = null
            })
            .fail(function () {
                console.log('error')
            })
            .always(function () {
                CommonTool.loaded()
            })
        }
    },
    watch: {
        cityId: function (data, old) {
            CommonTool.loading()
            var tmp = {}
            var self = this
            this.coords = []
            if (data === 0 && data === '0') {
                this.allOilstation.forEach(function (elem) {
                    tmp = {}
                    tmp.lat = elem.lat
                    tmp.lon = elem.lon
                    tmp.items = []
                    tmp.title = elem.name
                    tmp.items.push({
                        key: '地址',
                        value: elem.address
                    }, {
                        key: '电话',
                        value: elem.phone
                    })
                    self.coords.push(tmp)
                })
            } else {
                console.log(data)
                this.allOilstation.forEach(function (elem) {
                    if (elem.city_id == data) {
                        console.log(elem.city_id)
                        tmp = {}
                        tmp.lat = elem.lat
                        tmp.lon = elem.lon
                        tmp.items = []
                        tmp.title = elem.name
                        tmp.items.push({
                            key: '地址',
                            value: elem.address
                        }, {
                            key: '电话',
                            value: elem.phone
                        })
                        self.coords.push(tmp)
                    }
                })
            }
            this.mapCityName = this.cityMap[this.cityId]
        }
    }

}

</script>


<style type="text/css" scoped>

#weiche {
    width: 100%;
    height: 100%;
}
#searchCity {
    width: 300px;
    position: absolute;
    top: 20px;
    z-index: 10;
    left: 70px;
}
#city-input{
    box-shadow: 0px 0px 10px 0px black;
}
#city-input:focus {
    border: 0px solid black;
}
.oilstationCount {
    display: block;
    position: absolute;
    left: 370px;
    top: 26px;
    font-size: 16px;
    color: #00cc56;
}
#searchOilDiv {
    width: 300px;
    height:80%;
    overflow: scroll;
    position: absolute;
    top: 60px;
    left: 70px;
}
</style>



