<template>
    <div id="weiche-map"></div>
</template>

<script>
import mapLoader from './AMap.js'
import CommonTool from '../../Index/Lib/CommonTool.js'
export default {
    data: function () {
        return {
            markers: [],
            infoWindow: null,
            map: null,
            clustererHandel: null
        }
    },
    mounted: function () {
        this.$nextTick(() => {
            var self = this
            mapLoader('5636be464ebf8150b1689fb92a596172&plugin=AMap.ToolBar,AMap.Scale,AMap.MarkerClusterer').then(AMap => {
                window.AMap = AMap
                self.map = new AMap.Map('weiche-map', {
                    zoom: 5,
                    lang: 'zh_cn',
                    rotateEnable: true,
                    center: [108.125591, 38.893126],
                    mapStyle: 'fresh'
                })
                AMap.plugin(['AMap.ToolBar', 'AMap.Scale'], function () {
                    var toolBar = new AMap.ToolBar()
                    var scale = new AMap.Scale()
                    self.map.addControl(toolBar)
                    self.map.addControl(scale)
                })
            })

            this.markers = []
            $(document).on('click', '.closeInfoWindow', function (res) {
                self.map.clearInfoWindow()
            })
        })
    },
    props: {
        points: Array,
        clusterer: String,
        city: String
    },
    methods: {
        makeMarker: function () {
            var self = this
            try {
                this.cancelMarkerMap()
                this.infoWindow = new window.AMap.InfoWindow({
                    isCustom: true,
                    offset: new window.AMap.Pixel(120, -20)
                })
                self.markers = []
                this.points.forEach(function (elem, index) {
                    var tmpMarker = new window.AMap.Marker({
                        position: [elem.lon, elem.lat],
                        map: self.map
                    })

                    tmpMarker.title = elem.title
                    tmpMarker.items = elem.items
                    tmpMarker.on('click', self.clickMarker)
                    self.markers.push(tmpMarker)
                })
                if (this.clusterer === 'true') {
                    if (this.clustererHandel) {
                        this.clustererHandel.clearMarkers()
                    }
                    window.AMap.plugin(['AMap.MarkerClusterer'], function () {
                        self.clustererHandel = new window.AMap.MarkerClusterer(self.map, self.markers, {
                            minClusterSize: 5
                        })
                    })
                }
            } catch (e) {
                setTimeout(function () {
                    self.makeMarker()
                }, 2000)
            }
        },
        cancelMarkerMap: function () {
            this.markers.forEach(function (elem) {
                elem.setMap()
            })
        },
        clickMarker: function (e) {
            var template = '<div class="panel panel-info amap-infowindow" style="position: relative;">' +
                    '<header class="panel-heading">' +
                        '+ e.target.title +' +
                        '<span class="tools pull-right">' +
                           '<a class="fa fa-times closeInfoWindow" href="javascript:;"></a>' +
                        '</span>' +
                    '</header>' +
                    '<ul class="list-group" style="border: 1px solid silver; border-top-width: 0px;overflow: hidden;">'

            e.target.items.forEach(function (elem) {
                template += '<li class="list-group-item clearfix">' +
                        '<p class="col-md-3 no-margin no-padding">' + elem.key + '</p>' +
                        '<p class="col-md-9 no-margin no-padding text-elips">' + elem.value + '</p>' +
                    '</li>'
            })
            template += '</ul>' +
                    '<img class="amap-sharpe" src="https://martin-upload.b0.upaiyun.com/web/2016/09/f41bd1e2089532d7c0e440106c164c6a.png" alt="">' +
                '</div>'
            this.infoWindow.setContent(template)
            this.infoWindow.open(this.map, e.target.getPosition())
        },
        setCity: function () {
            try {
                if (this.city === undefined) {
                    this.map.setCenter([108.125591, 38.893126])
                    this.map.setZoom(5)
                    CommonTool.loaded()
                } else {
                    this.map.setCity(this.city, function () {
                        CommonTool.loaded()
                    })
                }
            } catch (e) {
                var self = this
                setTimeout(function () {
                    self.map.setCity(self.city, function () {
                        CommonTool.loaded()
                    })
                }, 2000)
            }
        }
    },
    watch: {
        points: function () {
            this.makeMarker()
        },
        city: function () {
            this.setCity()
        }
    }
}

</script>

<style type="text/css">
    #weiche-map {
        width: 100%;
        height: 100%;
        background: white;
    }
    .amap-infowindow {
        width: 400px;
    }
    .text-elips {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .no-padding-bt {
        padding-top: 0px;
        padding-bottom: 0px;
    }
    .no-margin {
        margin: 0px;
    }
    .no-padding {
        padding: 0px;
    }
    .amap-sharpe {
        position: absolute;
        bottom: -21px;
        left: 20%;
    }
</style>



